.Clock {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 3em;
  padding-left: 1em;
  padding-right: 1em;
  width: 60vw;
}

.lateEvening {
  background-color: #ff9999;
  color: black;
}

.earlyEvening {
  background-color: #ffffcc;
  color: black;
}
.work {
  background-color: #ccffcc;
  color: black;
}
.morning {
  background-color: #ffffcc;
  color: black;
}
.night {
  background-color: black;
  color: white;
}

.TimeBlockSegment {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Time {
  font-family: monospace;
}

.Timezone {
  text-align: right;
  align-items: right;
  font-size: 0.4em;
}

.multi-city {
  font-size: 0.8em;
}

@media only screen and (max-width: 768px) {
  .Clock {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 3em;
    padding-left: 0.5em;
    padding-right: 0.5em;
    width: calc(100vw - 1em);
  }
}
